
body {
  background-color: #fbf9fe;
  width: 10rem!important;
  height: 100%;
  margin: 0 auto;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
.leftMenu {
  fill: #fff;
  position: relative;
  top: -8px;
  left: -3px;
}
.planSwiper .vux-swiper,
.irriSwiper .vux-swiper {
  height: 100%!important;
  background-color: #fbf9fe!important;
}
.planSwiper .vux-swiper-item {
  overflow-y: auto;
}
.vux-swiper-desc {
  height: 40px!important;
  background-image: none!important;
  line-height: 40px!important;
  padding: 0!important;
}
.vux-header-title {
  margin: 0 65px!important;
}
.drawer-left .weui-cells:before,
.drawer-left .weui-cells:after,
.drawer-left .weui-cell:before {
  border: none!important;
}
.center {
  text-align: center;
  padding-top: 20px;
  color: #fff;
  font-size: 18px;
}
.center img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 4px solid #ececec;
}
.vux-bg-blur {
  width: 120%!important;
  height: 110%!important;
}
.weui-form-preview__label {
  height: 30px;
  text-align: center;
}
.username {
  color: #fff;
}
.sideMenu .vux-label {
  padding-left: 50px;
}
.home .vux-label {
  background: url(../assets/home.png) 30px center no-repeat;
}
.plans .vux-label {
  background: url(../assets/plans.png) 30px center no-repeat;
}
.compile .vux-label {
  background: url(../assets/compile.png) 30px center no-repeat;
}
.setting .vux-label {
  background: url(../assets/setting.png) 30px center no-repeat;
}
.exit {
  position: absolute;
  left: 0;
  bottom: 0px;
  width: 170px;
  margin: 15px;
}
.personalInfo label.weui-label {
  width: 3rem!important;
  text-align: left;
}
.sex .weui-cell__bd {
  position: relative;
}
.sex .vux-label-desc {
  position: absolute;
  right: 0.2rem;
  top: 0;
  font-size: 0.5rem;
}
.savePanel .weui-cells {
  width: 9rem!important;
  margin: 1rem auto;
}
.router-view {
  width: 100%;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  transition: all 500ms;
  height: 100%;
  top: 46px;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.menu-title {
  color: #888;
}



